.header {
    width: 100%;
    height: 5rem;
    background-color: #f8f9fa;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
}

.logo {
    font-family: 'Lato', sans-serif;
    font-size: 1.5rem;
    color: black;
    margin: 0;
}

.header ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: baseline;
}

.header a {
    text-decoration: none;
    color: rgba(0,0,0,.5);
    font-weight: bold;
}

.header button {
    font: inherit;
    background-color: transparent;
    border: none;
    color: rgba(0,0,0,.5);
    font-weight: bold;
    padding: 0.5rem 1.5rem;
    cursor: pointer;
}


.header a:hover {
    color: black;
}

.header button:hover {
    color: black;
}

